const checkboxThemeSwitcher = document.querySelector('.theme-switch__toggle');
checkboxThemeSwitcher.addEventListener('click', isChecked)

function isChecked(event){
  // event.currentTarget.setAttribute('checked', true);
  // localStorage.setItem('checkbox-state', `${checkboxThemeSwitcher.checked}`)
  // console.dir(checkboxThemeSwitcher.checked)
  // bodyRef.classList.add(`${Theme.LIGHT}`);
  if(checkboxThemeSwitcher.checked){

    bodyRef.classList.replace(`${Theme.LIGHT}`, `${Theme.DARK}`) 
    localStorage.setItem('theme', `${bodyRef.classList.value}`)
    // event.currentTarget.setAttribute('checked', true);
    // localStorage.setItem('checkbox-state', `${checkboxThemeSwitcher.checked}`)
    
  } else {

    bodyRef.classList.replace(`${Theme.DARK}`, `${Theme.LIGHT}`)
    localStorage.setItem('theme', `${bodyRef.classList.value}`)
    // event.currentTarget.setAttribute('checked', false)
    // localStorage.setItem('checkbox-state', `${checkboxThemeSwitcher.checked}`)

    // localStorage.setItem('checkbox-state', `${checkboxThemeSwitcher.checked}`)
  }
  
}

